body {
  background: #F5F5F5;
  margin: 0 auto;
  max-width: 1200px;
  min-width: 900px;
  padding: 0 50px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

main {
  position: relative;
}
main article.open {
  height: 100%;
  border: 0;
}
main article.open .head {
  font-size: 36px;
  font-weight: 700;
  color: #424242;
  cursor: default;
  margin: 20px 0;
}
main article.open .content {
  font-size: 16px;
  overflow: hidden;
}
main .comments {
  margin-top: 25px;
  position: relative;
}
main .comments .comments-count {
  top: -25px;
}
main .comments .comment {
  background: #ffffff;
  margin: 10px 0;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
main .comments .comment .remove-comment {
  position: -webkit-sticky;
  position: sticky;
  float: right;
  font-size: 10px;
  width: 20px;
  height: 20px;
}
main .comments .comment .author {
  color: #0D47A1;
}
main .comments .comment .content {
  padding-left: 15px;
}
main .comments #add-comment textarea {
  resize: none;
  font-size: 14px;
  border-radius: 5px;
  padding: 5px;
  border: 1px solid #BDBDBD;
  display: block;
  margin: 10px 0;
}
main .comments #add-comment input {
  color: #ffffff;
  width: 100px;
  height: 30px;
  border-radius: 10px;
  background: #4CAF50;
}
main .comments #add-comment input:disabled {
  background: #E0E0E0;
}
main button {
  color: #ffffff;
  width: 30px;
  height: 30px;
  border-radius: 5px;
  font-size: 20px;
  position: absolute;
  background: #f44336;
  top: -40px;
  right: 0;
}
main button.close {
  left: 0;
  background: #B0BEC5;
  padding-top: 1px;
  width: 100px;
}
main button.edit {
  right: 40px;
  background: #B0BEC5;
}

