.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.25);
}
.popup form {
  position: relative;
  width: 900px;
  height: 500px;
  margin: 65px auto;
  background: #ffffff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 100px rgba(0, 0, 0, 0.2);
}
.popup form button {
  height: 40px;
  width: 120px;
  font-size: 14px;
  background: rgba(0, 0, 0, 0.15);
  top: 20px;
  left: 20px;
}
.popup form textarea {
  resize: none;
  font-size: 14px;
  border-radius: 5px;
  padding: 5px;
  border: 1px solid #757575;
  display: block;
  margin: 10px auto;
}
.popup form input {
  position: absolute;
  right: 20px;
  top: 20px;
  height: 40px;
  color: #ffffff;
  width: 120px;
  border-radius: 10px;
  background: #4CAF50;
}
.popup form input:disabled {
  background: #E0E0E0;
}

/*# sourceMappingURL=HandleArticle.css.map */

* {
  border: 0;
  margin: 0;
  padding: 0;
  outline: 0;
}

button {
  cursor: pointer;
}

button:hover {
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.2);
}

button:active {
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.3) !important;
}

header {
  margin: 0;
  height: 65px;
  width: 100%;
  border-bottom: 1px solid #BDBDBD;
}
header .logo {
  float: left;
  font-family: "Courier New", Courier, monospace;
  vertical-align: middle;
  font-size: 36px;
  line-height: 65px;
}
header button {
  color: #ffffff;
  border-radius: 5px;
  background: #B0BEC5;
  font-size: 14px;
  float: right;
  height: 30px;
  width: 95px;
  margin: 17px 0;
}

main {
  margin-top: 50px;
}
main article {
  margin: 10px 0;
  border-radius: 10px;
  background: #ffffff;
  padding: 10px 20px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  position: relative;
  height: 150px;
  border-bottom: 1px solid #BDBDBD;
}
main article .head {
  font-size: 36px;
  font-weight: 700;
  color: #212121;
  cursor: pointer;
  margin: 20px 0;
}
main article .brief {
  font-size: 20px;
  margin-bottom: 20px;
}
main article .comments-count {
  position: absolute;
  color: blue;
  font-size: 15px;
  top: 15px;
  right: 20px;
}
main article .remove {
  cursor: pointer;
}

/*# sourceMappingURL=MainPage.css.map */

